img {
  object-fit: contain;
  height : 100%;
  width : 100%;
  cursor: pointer;
}

.views {
  height : 30px;
  width : 100px;
  display : none;
  flex-direction: row;
  justify-content: space-between;
}

.view {
  height : 30px;
  width : 30px;
  margin-left : 10px;
  padding: 5px;
  border-radius: 50px;
}

.view:hover {
  background: lightgrey;
}

.search {
  width : 100%;
  height : 20vh;
  display : flex;
  flex-direction : column;
  align-items: center;
  justify-content: space-between;
  margin-bottom : 30px;
}


@media only screen and (min-width : 800px) {

  .views {
    display : flex;
  }

  .search {
    flex-direction: row;
    width : 80%;
  }

}
